import React, { useState } from 'react'
import SuperRange from './common/c7-SuperRange/SuperRange'
import SuperDoubleRange from './common/c8-SuperDoubleRange/SuperDoubleRange'
import s from './HW11.module.css'

export type ValuePropsType = {
    value1: number
    value2: number
}

function HW11() {
    const [value1, setValue1] = useState(0)
    const [value2, setValue2] = useState(100)

    const values: ValuePropsType = {
        value1: value1,
        value2: value2
    }

    const onChangeRange = (value: number) => {
        if (value <= value2) {
            setValue1(value)
        }
    }

    const onChangeDoubleRange = (newValue: any) => {
        setValue1(newValue[0])
        setValue2(newValue[1])
    }

    return (
        <div>
            <hr />
            homeworks 11

            {/*should work (должно работать)*/}
            <div className={s.block}>
                <span className={s.span}>{value1}</span>
                <SuperRange onChangeRange={onChangeRange} value={value1}
                />
                <span className={s.span}></span>
            </div>

            <div className={s.block}>
                <span className={s.span}>{value1}</span>
                <SuperDoubleRange onChangeDoubleRange={onChangeDoubleRange} value1={value1} value2={value2} values={values}
                // сделать так чтоб value1 и value2 изменялось
                />
                <span className={s.span}>{value2}</span>
            </div>

            <hr />
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperRange/>*/}
            {/*<AlternativeSuperDoubleRange/>*/}
            <hr />
        </div>
    )
}

export default HW11
